import {
  AsyncResult,
  SpringConfig,
  SpringProps,
  SpringValue,
  easings,
} from "@react-spring/web";
import { TAnchor, ThemeEnumType } from "./types";

export const PADDING = 6 as const;
export const GAP = 4 as const;

/** Minimum size of the Dynamic-Menu in pixels. */
export const DEFAULT_SIZE = 52 as const;
export const FORM_ROW_HEIGHT = 36 as const;
/** in ms */
export enum EAnimDuration {
  FAST = 150,
  STANDARD = 300,
  SLOW = 500,
}

export enum EStage {
  enter = "enter",
  default = "default",
  exit = "exit",
}

export const configLinearFast = {
  easing: easings.linear,
  duration: EAnimDuration.FAST,
} as SpringConfig;
export const configEaseInOutStandard = {
  easing: easings.easeInOutQuad,
  duration: EAnimDuration.STANDARD,
} as SpringConfig;
export const configEaseOutStandard = {
  easing: easings.easeOutQuad,
  duration: EAnimDuration.STANDARD,
} as SpringConfig;
export const configEaseInOutSlow = {
  easing: easings.easeInOutQuad,
  duration: EAnimDuration.SLOW,
} as SpringConfig;
export const configEaseOutSlow = {
  easing: easings.easeOutQuad,
  duration: EAnimDuration.SLOW,
} as SpringConfig;
export const configExaggerated = {
  friction: 15,
  tension: 100,
  mass: 1,
} as SpringConfig;

type Result = AsyncResult<SpringValue<number>> | Promise<any>;
export interface Control {
  x?: number;
  y?: number;
  width?: number;
  height?: number;
  config?: SpringProps<any>;
  opacity?: number;
}
export interface IDynamicMenuControllerContext {
  /** @deprecated use scaleSizeTo instead  */
  startSizeTo?: (control: Control, delay?: number) => Result;
  /** @deprecated use scaleSizeTo instead  */
  returnToInitialStyle?: () => Result;
  scaleSizeTo?: (control: Control) => Result;
  /** @deprecated use scaleSizeTo instead  */
  scaleToInitialSize?: (config?: SpringProps<any>) => Result;
  setSizeTo?: (control: Control) => void;
  menuX?: SpringValue<number>;
  menuY?: SpringValue<number>;
  menuWidth?: SpringValue<number>;
  menuHeight?: SpringValue<number>;
  menuOpacity?: SpringValue<number>;
}

export interface IDymenuThemeContext {
  currentTheme: ThemeEnumType;
}

export interface IDynamicActorController {
  x?: SpringValue<number>;
  y?: SpringValue<number>;
  opacity?: SpringValue<number>;
  scale?: SpringValue<number>;
}

export function getAnchorOffset(
  width: number,
  height: number,
  anchor: TAnchor
): { x: number; y: number } {
  switch (anchor) {
    case "top-left":
      return { x: 0, y: 0 };
    case "top":
      return { x: width / 2, y: 0 };
    case "top-right":
      return { x: width, y: 0 };
    case "left":
      return { x: 0, y: height / 2 };
    case "center":
      return { x: width / 2, y: height / 2 };
    case "right":
      return { x: width, y: height / 2 };
    case "bottom-left":
      return { x: 0, y: height };
    case "bottom":
      return { x: width / 2, y: height };
    case "bottom-right":
      return { x: width, y: height };
  }
}

export const cssStatic = {
  background: "none",
  cursor: "default",
  pointerEvents: "none",
} as const;
